<template>
  <div
    id="card-list"
    :class="animateList"
    :style="{ animationDuration: duration + 's' }"
  >
    <div class="card-layer mb-3">
      <div
        class="layer-one py-5 px-5"
        :class="[cardCutting, openedMenu, setColor]"
      >
        <div
          class="downer d-flex align-start"
          :class="$vuetify.breakpoint.lgAndUp ? 'mb-3' : ''"
        >
          <div
            class="left-side d-flex flex-column text-left"
            style="width: 80%"
          >
            <span
              class="font-weight-black"
              :class="[
                setDone,
                $vuetify.breakpoint.mdAndUp ? 'headline' : 'title'
              ]"
            >
              {{ agenda }}
            </span>
            <span
              :class="[$vuetify.breakpoint.mdAndUp ? 'subtitle-1' : 'body-1']"
              style="fontFamily: 'Roboto Condensed', sans-serif !important "
            >
              {{ time }}
            </span>
            <span
              class="font-weight-bold"
              :class="[
                $vuetify.breakpoint.lgAndUp ? 'subtitle-1' : 'body-1',
                $vuetify.breakpoint.lgAndUp ? 'mt-5' : ''
              ]"
            >
              {{ date }}
            </span>
          </div>
          <v-spacer></v-spacer>
          <div
            class="rigth-side d-flex flex-column"
            v-if="$vuetify.breakpoint.mdAndDown"
          >
            <v-btn icon @click="open" v-if="menuStatus">
              <v-icon>blur_on</v-icon>
            </v-btn>
            <v-btn icon @click="close" v-else>
              <v-icon>keyboard_arrow_right</v-icon>
            </v-btn>
          </div>
        </div>
        <v-divider v-if="$vuetify.breakpoint.lgAndUp"></v-divider>
        <div class="upper d-flex mt-3" v-if="$vuetify.breakpoint.lgAndUp">
          <v-btn
            small
            text=""
            @click="doneEvent"
            :class="done ? 'disabled' : ''"
          >
            <v-icon small class="mr-1">done_all</v-icon>
            <span>Done</span>
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn icon small @click="$emit('delEvent', id)">
            <v-icon>delete</v-icon>
          </v-btn>
        </div>
      </div>
      <div class="layer-two" v-show="$vuetify.breakpoint.mdAndDown">
        <div class="menu-card d-flex flex-column py-2" style="height: 100%">
          <div class="menu-up d-flex flex-column">
            <v-btn icon small @click="doneEvent" :disabled="done">
              <v-icon>done_all</v-icon>
            </v-btn>
          </div>
          <div class="menu-down mt-auto">
            <v-btn icon small @click="$emit('delEvent', id)">
              <v-icon>delete</v-icon>
            </v-btn>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AgendaList',
  components: {
    // VIcon: () => import('vuetify/lib'),
    // VBtn: () => import('vuetify/lib'),
    // VDivider: () => import('vuetify/lib')
  },
  props: {
    id: Number,
    agenda: String,
    time: String,
    date: String,
    bg: Number,
    done: Boolean,
    animateList: String
  },
  data: () => ({
    size: null,
    openedMenu: '',
    menuStatus: true
  }),
  computed: {
    sizeRandom() {
      return this.$vuetify.breakpoint.mdAndUp
        ? `${[this.id, this.sizeCom]}`
        : '';
    },
    sizeCom() {
      let random = val => {
        switch (val) {
          case 1:
            return 'big';
          case 2:
            return 'medium';
          case 3:
            return 'small';
        }
      };
      return random(this.size);
    },
    cardCutting() {
      return this.$vuetify.theme.dark ? 'dark-cut' : 'light-cut';
    },
    setColor() {
      let color = v => {
        switch (v) {
          case 0:
            return 'white-bg';
          case 1:
            return 'warning-bg';
          case 2:
            return 'info-bg';
        }
      };
      return color(this.bg);
    },
    setDone() {
      return this.done ? 'done' : '';
    },
    duration() {
      return Math.abs(Math.random() * 1.5);
    }
  },
  methods: {
    methods() {
      const random = Math.round(Math.random() * 3);
      this.size = random;
    },
    open() {
      this.openedMenu = 'opened-menu';
      this.menuStatus = false;
    },
    close() {
      this.openedMenu = '';
      this.menuStatus = true;
    },
    doneEvent() {
      let event = {
        id: this.id,
        task: this.agenda,
        timestamp: `${this.date + ' ' + this.time}`,
        bg: this.bg,
        done: true
      };
      this.$emit('doneEvent', event);
    }
  }
};
</script>

<style lang="scss" scoped>
.done {
  text-decoration-line: line-through;
  opacity: 0.6;
}
.card-layer {
  position: relative;
  .layer-one {
    transition: 0.3s ease-out;
    z-index: 1;
    border-radius: 0.5vw;
    position: relative;
    .left-side {
      transition: 0.5s;
    }
  }

  .opened-menu {
    transform: translateX(-40px);
    .left-side {
      opacity: 0.5;
      transition: 0.3s;
    }
  }
  .layer-two {
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
  }
  .dark-cut::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border-top: 15px solid #102d37;
    border-right: 15px solid transparent;
    width: 0;
  }
  .dark-cut::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom: none;
    border-top: 15px solid transparent;
    border-right: 15px solid #102d37;
  }
  .light-cut::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border-top: 15px solid #f3f8f3;
    border-right: 15px solid transparent;
    width: 0;
  }
  .light-cut::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom: none;
    border-top: 15px solid transparent;
    border-right: 15px solid #f3f8f3;
  }
  .white-bg {
    background-color: #ffffff;
    span {
      color: #02779e !important;
    }
    i {
      color: #02779e;
    }
    .disabled {
      opacity: 0.5;
      cursor: default;
    }
    hr {
      border-color: #02779e !important;
      opacity: 0.2;
    }
  }
  .warning-bg {
    background-color: #f8685f;
    span {
      color: #f3f8f3;
    }
    i {
      color: #f3f8f3;
    }
    .disabled {
      opacity: 0.5;
      cursor: default;
    }
    hr {
      border-color: #f3f8f3 !important;
      opacity: 0.2;
    }
  }
  .info-bg {
    background-color: #02779e;
    span {
      color: #f3f8f3;
    }
    i {
      color: #f3f8f3;
    }
    .disabled {
      opacity: 0.5;
      cursor: default;
    }
    hr {
      border-color: #f3f8f3 !important;
      opacity: 0.2;
    }
  }
}
</style>
